{{item.name}}\n          </router-link>\n        </div>\n      </div>\n      <table class=\"table InsuranceTab\">\n        <thead>\n          <tr>\n            <th>id</th>\n            <th>公司名称</th>\n            <th>录入时间</th>\n            <th>操作</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr v-for=\"item in list\">\n            <td class=\"t-l\">{{item.id}}</td>\n            <td>{{item.company}}</td>\n            <td>{{item.create_time}}</td>\n            <td>\n              <a href=\"javascript:;\"\n                 class='col-0ACD00'\n                 @click=\"modify(item.id,item.company)\">修改</a>\n              <a href=\"javascript:;\"\n                 class='col-FA644B'\n                 @click=\"del(item.id,item.company)\">删除</a>\n            </td>\n          </tr>\n        </tbody>\n      </table>\n      <el-dialog :title=\"DialogState==1?'添加保险公司':'修改保险公司'\"\n                 :visible.sync=\"companyDialogVisible\"\n                 width=\"30%\"\n                 center\n                 @close=\"close\">\n        <div class=\"company\">\n          <span>请输入公司:</span>\n          <span>\n            <el-input v-model=\"company\"\n                      placeholder=\"请输入保险公司名称\"></el-input>\n          </span>\n        </div>\n        <span slot=\"footer\"\n              class=\"dialog-footer\">\n          <el-button type=\"primary\"\n                     @click=\"SureAdd\">{{DialogState==1?'确定':'修改'}}</el-button>\n        </span>\n      </el-dialog>\n      <!-- 分页 -->\n      <div :class=\"list.length<=7?'page-nav':'page_center'\"\n           v-show=\"list.length>0\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      page: 1, //显示的页码数\n      pageCount: 1,\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      company: '', //输入的公司名称\n      companyDialogVisible: false,\n      DialogState: 1, //控制弹框的状态\n      currentID: 0,//当前列表id\n    }\n  },\n  created () {\n    this.init();\n  },\n  methods: {\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n    init () {\n      this.$axios.post('admin/SystemSetup/policyList', { token: this.token, page: this.page })\n        .then(res => {\n\n          this.list = res.data.data.list;\n          this.pageCount = res.data.data.rows;\n        }).catch(err => { })\n    },\n    //点击添加\n    addCompany () {\n      this.companyDialogVisible = true\n      this.DialogState = 1\n    },\n    //点击确定进行添加\n    SureAdd () {\n      if (!this.company) {\n        layer.msg('请输入公司名称', { time: 1500 })\n        return;\n      }\n      let obj = { token: this.token, company: this.company };\n      if (this.DialogState == 2) obj.id = this.currentID;\n      this.$axios.post('admin/SystemSetup/policyCompany', obj).then(res => {\n        if (res.data.code == 1) {\n          layer.msg(res.data.msg, { time: 1500 }, (() => {\n            this.companyDialogVisible = false\n            this.init()\n          }))\n        } else {\n          layer.msg(res.data.msg, { time: 1500 })\n        }\n      })\n    },\n    close () {\n      this.company = ''\n    },\n    //修改\n    modify (id, company) {\n      this.DialogState = 2\n      this.companyDialogVisible = true\n      this.company = company\n      this.currentID = id\n    },\n    del (id, company) { //删除\n      this.$confirm('您确定要永久删除此数据吗,是否继续?', '提示', {\n        confirmButtonText: '确定',\n        cancelButtonText: '取消',\n        type: 'warning'\n      }).then(() => {\n        this.$axios.post('admin/SystemSetup/comdel', { token: this.token, id: id, company: company }).then(res => {\n          if (res.data.code == 1) {\n            layer.msg(res.data.msg, { time: 1000 }, (() => {\n              this.init()\n            }))\n          } else {\n            layer.msg(res.data.msg, { time: 1000 })\n          }\n        }).catch(err => console.log(err))\n      }).catch(() => { })\n    }\n\n  },\n  mounted () {\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].name == '保险公司列表') {\n              this.seCurId = arr[i].id;\n              this.threeAuthList = arr[i].son;\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$message.error(res.data.msg)\n\n        }\n      })\n      .catch(err => {\n        throw (err)\n      })\n  },\n}\n</script>\n<style scoped>\n.addbtn {\n  float: right;\n  margin-right: 100px;\n  margin-top: 20px;\n}\n.InsuranceTab th {\n  width: 25% !important;\n}\n.InsuranceTab tbody tr td a {\n  margin-right: 0;\n}\n\n.company > span {\n  display: inline-block;\n  height: 50px;\n  line-height: 50px;\n  \n}\n.company > span:last-child {\n  width: 80%;\n}\n.Insurance table a {\n  margin-left: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

